}

.fade-enter,
.fade-appear {
    opacity: 0;
    -webkit-transition: opacity 1s ease-in;
    transition: opacity 1s ease-in;
}

.fade-enter-active,
.fade-apper-active {
    opacity: 1;
}

.fade-enter-done {
    opacity: 1;
}

.fade-exit {
    opacity: 1;
}

.fade-exit-active {
    /*opacity: 0;*/
    /*transition: opacity 1s ease-in;*/
    opacity: 1;
}

.fade-exit-done {
    /*opacity: 0;*/
    opacity: 1;
}

